"use server";

import { CreateContact, ContactsApi } from "@getbrevo/brevo";

export async function addEmailToBrevo(formData: FormData) {
  const email = formData.get("email") as string;

  if (!email) {
    console.error("Email is required");
    return;
  }

  if (!process.env.BREVO_API_KEY) {
    console.error("BREVO_API_KEY is not configured");
    return;
  }

  try {
    // Configure Brevo API
    const apiInstance = new brevo.ContactsApi();
    apiInstance.setApiKey(
      brevo.ContactsApiApiKeys.apiKey,
      process.env.BREVO_API_KEY
    );

    // Create contact data
    const createContact = new brevo.CreateContact();
    createContact.email = email;
    createContact.updateEnabled = true; // Update if contact already exists

    // Add contact to Brevo
    const result = await apiInstance.createContact(createContact);

    console.log("Contact added successfully:", result.body.id);
  } catch (error) {
    console.error("Brevo API Error:", error);

    // Handle duplicate contact error (contact already exists) - this is not actually an error
    if (
      error instanceof Error &&
      error.message.includes("duplicate_parameter")
    ) {
      console.log("Email already subscribed");
      return;
    }

    // For other errors, log but don't crash
    console.error("Failed to subscribe to newsletter:", error);
  }
}
